const posts = [
  {
    id: 0,
    description: "Lake",
    imageLink:
      "https://webneel.com/wallpaper/sites/default/files/images/08-2018/3-nature-wallpaper-mountain.1280.jpg",
  },
  {
    id: 1,
    description: "Aliens???",
    imageLink: "https://s3.india.com/wp-content/uploads/2017/12/rocket.jpg",
  },
  {
    id: 2,
    description: "On a vacation!",
    imageLink:
      "https://fm.cnbc.com/applications/cnbc.com/resources/img/editorial/2017/08/24/104670887-VacationExplainsTHUMBWEB.1910x1000.jpg",
  },
  {
    id: 3,
    description: "Camping in Mountains",
    imageLink:
      "https://static2.tripoto.com/media/filter/nxl/img/375011/TripDocument/1521799065_1521799062132.jpg",
  },
];

function getPost() {
  return posts;
}

function addPost(newPost) {
  const post = {
    id: posts.length,
    description: newPost.description,
    imageLink: newPost.imageLink,
  };

  posts.push(post);
  return posts;
}

export { getPost, addPost };
